.titleBlock {
	padding: 0.3em 1em;
	font-size: 2.4em;
	color: white;
}

.pillNavigation button {
	background-color: dimgray;
	color: white;
	font-size: 1.3em;
	width: 1.7em;
	height: 1.7em;
}
.pillNavigation button.active {
	background-color: dimgray !important;
}
.pillFirst {
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
}
.pillLast {
	border-top-right-radius: 50%;
	border-bottom-right-radius: 50%;
}
button.themed.timer {
	font-size: 1.6em;
	width: 7em;
	border: medium solid #e5cb2c;
	background-color: white;
}
.timer.running {
	font-family: monospace;
}

